import {
  Document,
  Paragraph,
  TextRun,
  AlignmentType,
  HeadingLevel,
  convertInchesToTwip,
} from 'docx';

/**
 * Word 
 * @param patientInfo 
 * @param summary AI 
 * @returns Document 
 */
export function createSurgeonAssessmentDocument(
  patientInfo: {
    name: string;
    chartNumber: string;
  },
  summary: string
): Document {
  const doc = new Document({
    sections: [
      {
        properties: {
          page: {
            margin: {
              top: convertInchesToTwip(1),
              right: convertInchesToTwip(1),
              bottom: convertInchesToTwip(1),
              left: convertInchesToTwip(1),
            },
          },
        },
        children: [
          // 3
          new Paragraph({
            text: '',
            heading: HeadingLevel.TITLE,
            alignment: AlignmentType.CENTER,
            spacing: {
              before: 0,
              after: 100,
            },
            style: 'Title',
          }),
          new Paragraph({
            text: '',
            alignment: AlignmentType.CENTER,
            spacing: {
              before: 0,
              after: 0,
            },
          }),
          new Paragraph({
            text: '',
            alignment: AlignmentType.CENTER,
            spacing: {
              before: 0,
              after: 200,
            },
          }),

          // TAVI 
          new Paragraph({
            text: 'TAVI ',
            heading: HeadingLevel.HEADING_1,
            alignment: AlignmentType.CENTER,
            spacing: {
              before: 200,
              after: 300,
            },
          }),

          // AI 
          new Paragraph({
            children: [new TextRun({ text: summary, size: 24 })], // 12pt = 24 half-points
            spacing: {
              line: 360, // 1.5 
              before: 100,
              after: 400,
            },
            alignment: AlignmentType.JUSTIFIED, // 
          }),

          // 
          new Paragraph({
            text: '',
            spacing: { before: 400, after: 200 },
          }),

          // 
          new Paragraph({
            children: [
              new TextRun({
                text: ' ____________________',
                size: 24,
              }),
              new TextRun({
                text: '        __________',
                size: 24,
              }),
            ],
            spacing: {
              before: 200,
              after: 200,
            },
          }),

          // 
          new Paragraph({
            text: '',
            spacing: { before: 100, after: 100 },
          }),

          // 
          new Paragraph({
            children: [
              new TextRun({
                text: ' ____________________',
                size: 24,
              }),
              new TextRun({
                text: '        __________',
                size: 24,
              }),
            ],
            spacing: {
              before: 200,
              after: 200,
            },
          }),
        ],
      },
    ],
    styles: {
      paragraphStyles: [
        {
          id: 'Title',
          name: 'Title',
          basedOn: 'Normal',
          run: {
            size: 32, // 16pt
            bold: true,
            font: {
              name: '',
              eastAsia: '',
            },
          },
        },
      ],
      default: {
        document: {
          run: {
            font: {
              name: 'Times New Roman',
              eastAsia: '', // 
            },
            size: 24, // 12pt = 24 half-points
          },
          paragraph: {
            spacing: {
              line: 360, // 1.5 
            },
          },
        },
      },
    },
  });

  return doc;
}

/**
 * 
 * @param patientName 
 * @param chartNumber 
 * @returns 
 */
export function generateFileName(
  patientName: string,
  chartNumber: string
): string {
  return `${patientName}${chartNumber} - `;
}
